/* HEADER */
header {
  padding: 1.25em 0;
  position: fixed;
  z-index: 10;
  width: 100%;
}

nav > div {
  display: flex;
  align-items: center;
  justify-content: space-between;
  position: relative;
}

.logo {
  border: solid 2px var(--secondary-theme);
  border-radius: 0.4em;
  position: relative;
  display: flex;
  justify-content: center;
  align-items: center;
  width: 50px;
  height: 50px;
  color: var(--secondary-theme);
}

header .logo {
  cursor: pointer;
  transition: all 0.25s ease;
}

header .logo:hover {
  color: white;
  border-color: white;
}

.logo .number {
  position: absolute;
  top: 0.1em;
  left: 0.25em;
  font-size: 0.75rem;
  font-weight: 600;
}

.logo .letter {
  font-family: "Patua One", cursive;
  font-size: 1.3rem;
}

.nav-links {
  list-style: none;
  display: flex;
  gap: 2em;
}

.nav-links li {
  color: var(--secondary-theme);
  font-weight: 600;
  cursor: pointer;
  transition: all 0.25s ease;
  font-size: 1rem;
}

.nav-links li:hover {
  color: white;
}

.nav-links.collapse {
  flex-direction: column;
  align-items: center;
  padding: 2em;
  gap: 3em;
}

nav button {
  background: none;
  border: none;
  cursor: pointer;
  display: none;
}

nav .icon {
  color: var(--secondary-theme);
  font-size: 1.5rem;
}

nav button:hover .icon {
  color: white;
}

/* MAIN */
section {
  position: relative;
}

.intro {
  background-color: black;
}

.intro .background-image {
  height: 100vh;
  width: 100%;
  max-width: 100vw;
  background-repeat: no-repeat;
  background-position: center;
  background-size: cover;
  margin: 0 auto;
  display: flex;
  flex-direction: column;
  gap: 0.5em;
  justify-content: center;
  align-items: center;
  transition: opacity 1.5s ease-in;
}

.intro .background-image p {
  font-size: 1.5rem;
  font-family: "Patua One", cursive;
  color: var(--secondary-theme);
  letter-spacing: 0.05rem;
}

.about {
  text-align: center;
  padding-top: 4em;
  padding-bottom: 4em;
}

.about h2 {
  color: var(--primary-theme);
}

.section-intro {
  max-width: 600px;
  margin: 0 auto 3em;
  font-size: 1.1rem;
}

.about .cards-container {
  display: flex;
  justify-content: center;
  gap: 1.5em;
}

.about .cards-container > * {
  flex-basis: 32%;
}

.about .cards-container .card {
  padding: 2.5em 1.5em;
  border-radius: 0.5em;
  display: flex;
  flex-direction: column;
  gap: 2.5em;
  align-items: center;
  background-color: #f6f1f1;
  color: rgba(0, 0, 0, 0.7);
}

.about .cards-container .card .description {
  color: rgba(0, 0, 0, 0.6);
}

.about .cards-container .card .icon-container {
  border-radius: 50%;
  background-color: white;
  display: flex;
  justify-content: center;
  align-items: center;
  width: 65px;
  height: 65px;
}

.about .cards-container .card .icon-container .icon {
  color: var(--primary-theme);
  font-size: 1.5rem;
}

.journey {
  background-color: #ebf3f7;
  padding-top: 4em;
  padding-bottom: 4em;
}

.journey .timeline {
  margin-top: 4em;
}

.journey .timeline .row {
  display: flex;
  justify-content: flex-start;
}

.journey .row > div {
  flex-basis: 100%;
  padding: 2em;
  display: flex;
  align-items: center;
}

.journey .row .container-lhs {
  justify-content: flex-end;
  border-right: solid 6px black;
  position: relative;
  padding-left: 0;
}

.journey .row .container-rhs {
  position: relative;
  padding-right: 0;
}

.journey .row .timeline-indicator {
  border-radius: 50%;
  width: 30px;
  height: 30px;
  border: solid 4px black;
  background-color: white;
  position: absolute;
}

.journey .row .container-lhs .timeline-indicator {
  right: -18px;
}

.journey .row .container-rhs .timeline-indicator {
  left: -18px;
}

.journey .row .timeline-end {
  width: 16px;
  height: 16px;
  background-color: black;
  border-radius: 50%;
  position: absolute;
}

.journey .row .container-lhs .timeline-end {
  right: -11px;
  bottom: 98%;
}

.journey .row .container-rhs .timeline-end {
  left: -11px;
  top: 98%;
}

.journey .row .content-container > div {
  background-color: white;
  border-radius: 0.5em;
  text-align: left;
  padding: 1em;
}

.journey .row .content-container .hidden-date {
  display: none;
}

.journey h3 {
  color: var(--primary-theme);
}

.projects {
  padding-top: 4em;
  padding-bottom: 4em;
  text-align: center;
}

.projects h2 {
  color: var(--primary-theme);
}

.projects .previews-container {
  display: flex;
  justify-content: center;
  gap: 1.5em;
}

.projects .previews-container .preview {
  flex-basis: 32%;
  border-radius: 0.5em;
  overflow: hidden;
  cursor: pointer;
  background-color: black;
  position: relative;
  display: flex;
  justify-content: center;
  align-items: center;
}

.projects .previews-container .preview span {
  position: absolute;
  color: white;
  font-weight: 600;
  visibility: hidden;
  display: flex;
  flex-direction: column;
  gap: 0.5em;
}

.projects .previews-container .preview:hover img {
  opacity: 0.3;
}

.projects .previews-container .preview:hover span {
  visibility: visible;
}

.cta {
  padding-top: 4em;
  padding-bottom: 4em;
  text-align: center;
  display: flex;
  flex-direction: column;
  align-items: center;
  gap: 0.5em;
}

.cta h2 {
  color: var(--primary-theme);
  margin: 0;
}

.cta a {
  padding: 0.5em 1em;
  color: var(--primary-theme);
  background-color: white;
  text-transform: capitalize;
  border: solid 2px var(--primary-theme);
  border-radius: 0.5em;
  font-size: 1.2rem;
  font-weight: 600;
  cursor: pointer;
  display: inline-block;
  margin-top: 1em;
  transition: all 0.5s ease;
}

.cta a:hover {
  color: white;
  background-color: var(--primary-theme);
}

/* FOOTER */
footer {
  background-color: #ebf3f7;
  padding-top: 4em;
  padding-bottom: 4em;
  margin-top: 4em;
}

footer > div {
  display: flex;
  flex-direction: column;
  gap: 1.5em;
  justify-content: center;
  align-items: center;
}

footer .logo {
  color: var(--primary-theme);
  border-color: var(--primary-theme);
  background-color: white;
}

footer .links-container {
  display: flex;
  gap: 1em;
}

footer .links-container .link {
  border-radius: 50%;
  height: 35px;
  width: 35px;
  display: flex;
  justify-content: center;
  align-items: center;
  border: solid 2px var(--primary-theme);
  background-color: white;
  cursor: pointer;
  transition: all 0.5s ease;
}

footer .links-container .link .icon {
  color: var(--primary-theme);
}

footer .links-container .link:hover {
  background-color: var(--primary-theme);
}

footer .links-container .link:hover .icon {
  color: white;
}

footer .copyright .icon {
  margin-right: 0.5em;
}

/* HEADINGS */
h1 {
  font-size: 4rem;
  font-family: "Patua One", cursive;
  color: white;
  letter-spacing: 0.05rem;
  text-align: center;
}

h2 {
  font-size: 2rem;
  font-weight: 600;
  color: black;
  margin-bottom: 1em;
  text-align: center;
}

h3 {
  font-size: 1.2rem;
  font-weight: 700;
  color: black;
  margin-bottom: 0.5em;
}

h4 {
  font-size: 1.1rem;
  font-weight: 600;
  color: var(--primary-theme);
  margin-bottom: 0.5em;
}

/* MEDIA QUERIES */
@media (max-width: 800px) {
  .about .cards-container {
    flex-wrap: wrap;
  }

  .about .cards-container > * {
    flex-basis: 48%;
  }

  .projects .previews-container {
    flex-wrap: wrap;
  }

  .projects .previews-container .preview {
    flex-basis: 48%;
  }
}

@media (max-width: 600px) {
  nav button {
    display: inline-block;
  }

  nav > div .nav-links {
    display: none;
  }

  .about .cards-container > * {
    flex-basis: 100%;
  }

  .intro .background-image p {
    font-size: 1.25rem;
  }

  h1 {
    font-size: 2.75rem;
  }
}

@media (max-width: 500px) {
  .projects .previews-container .preview {
    flex-basis: 100%;
  }

  .journey .timeline .row.reverse {
    flex-direction: row-reverse;
  }

  .journey .row.reverse .container-rhs {
    justify-content: flex-end;
    border-right: solid 6px black;
    position: relative;
    padding: 2em;
    padding-left: 0;
  }

  .journey .row.reverse .container-lhs {
    position: relative;
    justify-content: flex-start;
    border-right: none;
    padding: 2em;
    padding-right: 0;
  }

  .journey .row .content-container {
    margin-right: 2em;
  }

  .journey .row.reverse .container-rhs .timeline-indicator {
    left: auto;
    right: -18px;
  }

  .journey .row.reverse .container-lhs .timeline-indicator {
    right: auto;
    left: -18px;
  }

  .journey .row .date-container {
    flex-basis: 0;
  }

  .journey .row .content-container {
    flex-basis: 100%;
  }

  .journey .row .container-rhs .timeline-end {
    left: auto;
    right: -11px;
  }

  .journey .row .date-container span {
    display: none;
  }

  .journey .row .content-container .hidden-date {
    display: inline-block;
    font-size: 1.1rem;
    margin-bottom: 0.5em;
    font-weight: 600;
  }
}
